.options-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.icon-container {
    display: -ms-flexbox;
    display: flex;
    margin: 3rem;
    padding: 2rem;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    /* box-shadow: 0 2px 7px rgba(17, 199, 47, 0.425); */
    -webkit-box-shadow: 0 2px 7px rgba(78,76,80,.185);
            box-shadow: 0 2px 7px rgba(78,76,80,.185);
    text-decoration: none;
    color: #3e434d;
    cursor: pointer;
}

.icon-container > label {
    font-weight: bold;
}

.donate  .material-icons {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    border-radius: 50;
    margin: 3rem;
    font-size: 10rem;
    height: 13rem;
    width: 13rem;
    -webkit-transition: -webkit-transform 200ms ease-in-out;
    transition: -webkit-transform 200ms ease-in-out;
    -o-transition: transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
    cursor: pointer;
    /* box-shadow: 0 2px 10px rgba(17, 199, 47, 0.425); */

}

.icon-container:hover .material-icons {
    -webkit-transform: translateY(-15px);
        -ms-transform: translateY(-15px);
            transform: translateY(-15px);
}

.donate-options-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.options {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding-left: 0.5rem;
}

.options__header {
    font-size: 2rem;
    border-bottom: 2px solid #50c16f;
    padding-bottom: 1rem;
    font-weight: 550;
}

.options > div {
    margin-bottom: 2rem;
}

.options > div > input {
    margin-right: 0.5rem;
}

.options > div > label {
    color: #3e434d;
    font-size: 2rem;
}

.info {
    font-size: 1.5rem;
    margin-top: 0.5rem;
    margin-left: 2rem;
    color: #50c16f;
}

.checkout-button {
    color: white;
    background: #50c16f;
    border: none;
    padding: 1rem;
    font-size: 2rem;
    border-bottom: 4px solid #48ac62; 
    outline: none;
    -webkit-transition: -webkit-transform 200ms ease-out;
    transition: -webkit-transform 200ms ease-out;
    -o-transition: transform 200ms ease-out;
    transition: transform 200ms ease-out;
    transition: transform 200ms ease-out, -webkit-transform 200ms ease-out;
    width: 100%;
}

.checkout-button:disabled {
    background: #aaa;
    color: rgb(151, 151, 151);
    border-bottom: 4px solid rgb(87, 87, 87); 
}

.checkout-button:disabled:active {
    border-top: 4px solid rgb(87, 87, 87); 
}

.checkout-button:active {
    border-bottom: none;
    border-top: 4px solid #48ac62;
}

.error-info {
    color: red;
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.amount-form {
    display: -ms-flexbox;
    display: flex;
    margin: 3rem 0;
}

@media (max-width: 745px) {
    .options-container {
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .donate .material-icons {
        margin: 2rem;
    }
    .options > div {
        margin-bottom: 1rem;
    }
    .options > div > label {
        font-size: 1.5rem;
    }
    .amount-form {
        margin: 2rem 0;
    }
    .error-info {
        font-size: 1.3rem;
    }
}



