.floating-menu {
    border-radius: 50px;
    background: white;
    -webkit-box-shadow: 0 1px 10px rgba(78,76,80,.185);
            box-shadow: 0 1px 10px rgba(78,76,80,.185);
    position: fixed;
    top: 88%;
    right: 2rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 1rem;
    z-index: 1;
    -webkit-transition-property: top, right, border-radius, width, height;
    -o-transition-property: top, right, border-radius, width, height;
    transition-property: top, right, border-radius, width, height;
    -webkit-transition-duration: 500ms;
         -o-transition-duration: 500ms;
            transition-duration: 500ms;
    -webkit-transition-delay: 0ms, 0ms, 0ms, 400ms, 400ms;
         -o-transition-delay: 0ms, 0ms, 0ms, 400ms, 400ms;
            transition-delay: 0ms, 0ms, 0ms, 400ms, 400ms;
}

.floating-menu--expanded {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: start;
    width: 100%;
    height: 100%;
    padding: 0;
    top: 70px;
    right: 0;
    border-radius: 0;
}

.drawer {
    color: rgba(36, 178, 75, 0.8);
    font-size: 3.5rem;
}

.links-section--mobile {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.link--mobile {
    margin: 2rem 0;
}

@media (min-width: 745px) {
    .floating-menu {
        display: none;
    }
}



