.header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin: 0;
    background: white;
    color: #24b24b;
    height: 7rem;
    position: fixed;
    width: 100%;
    z-index: 1;
    -webkit-box-shadow: 0 2px 5px rgba(78,76,80,.185);
            box-shadow: 0 2px 5px rgba(78,76,80,.185);
}

.placeholder {
    -ms-flex-preferred-size: 12rem;
        flex-basis: 12rem;
}

.header-link {
    margin: 0;
    text-decoration: none;
    color: #24b24b;
    cursor: pointer;
}

.links-section {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: end;
        justify-content: end;
    justify-content: flex-end;
}

.link {
    padding: 0 4.5rem;
    color: #24b24b;
    text-decoration: none;
    display: -ms-flexbox;
    display: flex;
    height: 6.5rem;
    -ms-flex-align: center;
        align-items: center;
    border-bottom: 5px solid transparent;
    cursor: pointer;
    -webkit-transition: border-color 500ms ease-in-out;
    -o-transition: border-color 500ms ease-in-out;
    transition: border-color 500ms ease-in-out;
}

.link--active {
    /* color: #24b24b; */
    border-bottom: 5px solid #50c16f;
}

.link h4 {
    margin: 0;
}

.link--donate {
    margin: 0;
    margin-left: 7.2rem;
    padding: 1rem;
    border: 3px solid #24b24b;
    -webkit-transition: background 200ms ease-in-out;
    -o-transition: background 200ms ease-in-out;
    transition: background 200ms ease-in-out;
    -webkit-transition: color 300ms ease-in-out;
    -o-transition: color 300ms ease-in-out;
    transition: color 300ms ease-in-out;
}

.link--donate:hover {
    background: #24b24b;
    color: white;
}

@media (max-width: 745px) {
    .header-link {
        font-size: 3rem;
    }
    .desktop-only {
        display: none;
    }
    .placeholder {
        -ms-flex-preferred-size: 2rem;
            flex-basis: 2rem;
    }
    .link--donate {
        width: 15rem;
        text-align: center;
        padding: 0.6rem;
    }
}


